import React from 'react'

interface SpinnerProps {
    color?: 'red' | 'green' | 'blue' | 'black' | 'yellow'
    className?: string
}

export const Spinner = ({ color, className, ...props }: SpinnerProps) => {
    return <div className={`spinner-border ${className}`} style={{ color: color }} {...props}></div>
}
